.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 403px;
  padding: 15px  14px;

  font-family: $major-font;

  border: 1px solid #f3f3f3;
  border-radius: $border-radius;

  &__picture {
    width: 170px;
    height: 164px;

    padding: 12px 2px;
    border: 3px solid transparent;

    transition: border 1s, transform 1s;

    &:hover {
      border-color: #f3f3f3;
      transform: scale(1.2);
    }
  }

  &__name {
    width: 170px;
    height: 31px;
    margin-top: 30px;

    font-size: $font-size + 1px;
    font-weight: 500;
    line-height: 15px;
  }

  &__id {
    width: 101px;
    height: 13px;
    margin-top: 4px;

    font-size: 11px;
    white-space: nowrap;
  }

  &__rating {
    @include flex-space;
    margin-top: 15px;
  }

  &__reviews {
    margin-top: 1px;
    font-size: $font-size;
  }

  &__price {
    @include flex-space;
  }

  &__price-text {
    margin-top: 25px;
    font-size: $font-size;
  }

  &__value {
    margin-top: 20px;
    font-weight: bold;
    font-size: $font-size + 5px;
  }

  &__button {
    margin-top: 20px;
    width: 170px;
    height: 40px;

    font-size: $font-size + 3px;
    font-weight: bold;
    color: $background-color;

    background: $active-color;
    border: 1px solid $active-color;
    border-radius: $border-radius;

    transition: background-color 1s, color 1s;

    &:hover {
      background-color: $background-color;
      color: $active-color;
    }
  }
}
